/* STATIC STYLES (desktop-first) */

.portfolio-container {
  display: grid;
  grid-template-columns: 1fr;

  margin-top: -2rem;
}

.portfolio-item-desktop {
  display: flex;
  flex-direction: column;
}

.portfolio-item {
  display: grid;
  align-items: center;
  grid-template-columns: 40% 60%;

  max-width: 85vw;
  padding: 2.5rem;
}

.portfolio-item .portfolio-description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 90%;

  text-align: center;

  gap: 2rem;
}

.portfolio-item h3 {
  text-align: center;
  color: var(--color-primary);
}

.portfolio-item h4 {
  text-align: center;
  color: var(--color-primary-text-dark);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .portfolio-item {
    display: flex;
    flex-direction: column;
  }

  .portfolio-item .portfolio-description {
    margin-top: 0;
    margin-bottom: 2rem;
  }
}

