/* STATIC STYLES (desktop-first) */

.services-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  gap: 3rem;
}

.service-list {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 2rem;
}

.service-list li {
  display: flex;

  margin-bottom: 0.8rem;

  gap: 1rem;
}

.service-list-icon {
  min-width: 1rem;
  margin-top: 2px;

  color: var(--color-primary);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .services-container {
    grid-template-columns: 1fr 1fr;

    gap: 2rem;
  }
}

@media (max-width: 768px) {
}

@media (max-width: 576px) {
  .services-container {
    grid-template-columns: 1fr;

    gap: 1rem;
  }
}
