/* STATIC STYLES (desktop-first) */

:root {
  --portrait-height: 28vw;
  --portrait-width: calc(var(--portrait-height) / 1.5);
}

#hero {
  display: flex;
  justify-content: center;
  align-items: center;

  min-height: 100vh;
  padding: 3rem 0 12rem 0;

  overflow: hidden;
}

.hero-container {
  position: relative;

  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr;

  width: 100%;

  text-align: center;

  gap: 3rem;
}

.hero-content {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  margin-top: 15vh;

  text-align: center;
}

@property --rotate {
  inherits: false;
  initial-value: 150deg;
  syntax: '<angle>';
}

@keyframes spin {
  0% {
    --rotate: 0deg;
  }
  100% {
    --rotate: 360deg;
  }
}

.me {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 70%;
  max-width: 400px;
  margin: 0 auto 2rem;
}

.me-img {
  width: 100%;

  overflow: hidden;
  border-radius: 1.5rem;

  transform: rotate(-3deg);
  transition: all 400ms ease;
}

.me-img:hover {
  transform: rotate(0);
}

.me::after {
  content: '';
  position: absolute;
  z-index: -1;

  width: 100%;
  height: 100%;

  background-image: linear-gradient(var(--rotate), #4db5ff, #ffffff99 43%, #4db5ff66);
  filter: blur(calc(var(--portrait-height) / 8));
  opacity: 1;

  transform: scale(0.9);
  transition: opacity 0.7s;
  animation: spin 2.5s linear infinite;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .container {
    width: 85%;
  }

  .me {
    width: 40vw;
    max-width: 350px;
    margin: 0 auto 3rem;
  }
}

@media (max-width: 768px) {
  .hero-container {
    grid-template-columns: 1fr;

    gap: 1rem;
  }

  .hero-content {
    width: 100%;
    margin-top: 5vh;
  }

  .me {
    width: 25vw;
    margin: 0 auto 1.5rem;
  }
}

@media (max-width: 576px) {
  :root {
    --portrait-height: 60vw;
  }

  #hero {
    padding: 0 0 2rem 0;
  }

  .me {
    width: 50vw;
    max-width: 300px;
  }
}
