/* STATIC STYLES (desktop-first) */

.experience-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

  gap: 2rem;
}

.experience-card {
  padding: 2rem;

  text-align: center;
}

.experience-card h3 {
  margin-bottom: 1rem;

  text-align: center;
  white-space: nowrap;
  color: var(--color-primary);
}

.experience-card .experience-content {
  display: flex;
  flex-direction: column;
  align-items: center;

  gap: 0.5rem;
}

.experience-card .experience-details {
  display: flex;
  align-items: center;

  margin-bottom: 0.5rem;

  break-inside: avoid-column;
  gap: 0.5rem;
}

.experience-card .experience-details-icon {
  min-width: 1rem !important;

  color: var(--color-primary-taxt-dark);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .experience-container {
    grid-template-columns: 1fr;
  }

  .experience-container > div {
    width: 50%;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .experience-container > div {
    width: 70%;
  }

  .experience-content {
    padding: 0 1rem;

    columns: 10rem;
  }
}

@media (max-width: 576px) {
  .experience-container {
    gap: 1rem;
  }

  .experience-container > div {
    width: 90%;
  }

  .experience-content {
    columns: 12rem;
  }
}
