/* STATIC STYLES (desktop-first) */

.contact-container {
  display: flex;
  justify-content: center;

  width: 80%;
}

.contact-options {
  display: grid;
  grid-template-columns: 1fr 1fr;

  width: auto;

  gap: 6rem;
}

.contact-option {
  padding: 2rem;

  text-align: center;
}

.contact-option .contact-option-icon {
  margin-bottom: 0.5rem;
}

.contact-option h5 {
  margin-bottom: 1rem;
}

.contact-option a {
  display: inline-block;

  margin-top: 0.5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .contact-options {
    gap: 2rem;
  }
}

@media (max-width: 576px) {
  .contact-options {
    grid-template-columns: 1fr;

    width: 90%;

    gap: 2rem;
  }
}
