/* STATIC STYLES (desktop-first) */

.card3 {
  border: 1px solid transparent;
  border-radius: 2rem;

  background: var(--color-secondary-dark);

  transform: perspective(750px) translate3d(0px, -20px, -100px) rotateX(25deg) scale(0.95, 1);
  transition: all 400ms ease;
}

.card3:hover {
  border-color: var(--color-secondary);

  background: var(--color-blur);

  transform: translate3d(0px, 0, -120px);
}

